import {API_BASE_URL}  from "../api-config";
const ACCESS_TOKEN = "ACCESS_TOKEN";




export const call =  (api, method, request) => {
    let headers = new Headers({
        "Content-Type": "application/json",
    });

    //  ACESS TOKEN 
    const accessToken = localStorage.getItem(ACCESS_TOKEN);
    if(accessToken && accessToken !== null){
        headers.append("Authorization","Bearer "+accessToken);
    }

    let options = {
        headers: headers,
        url: API_BASE_URL + api,
        method: method,
    };
    if (request) {
        // GET method
        options.body = JSON.stringify(request);
    }
    return fetch(options.url, options)
        .then((response) =>
            response.json().then((json) => {
                if (!response.ok) {
                    // response.ok true   ,    .
                    return Promise.reject(json);
                }
                return json;
            })
        )
        .catch((error) => {
            //  
            console.log(error.status);
            if (error.status === 403) {
                window.location.href = "/login"; // redirect
            }
            return Promise.reject(error);
        });
}


export const signin = (userDTO) => {
    return call("/eple/v1/auth/signin","POST",userDTO)
        .then((response)=>{
            if(response.token){
                //   
                localStorage.setItem(ACCESS_TOKEN,response.token);

                //token   lecture  redirect
                window.location.href = "/";
            }
        });
}

export const signout = () => {
    localStorage.setItem(ACCESS_TOKEN, null);
    window.location.href = "/login";
}

export const signup = (userDTO) => {
    return call("/eple/v1/auth/signup","POST",userDTO);
}


